	}
	.blog__subscribe-box input {
		font-size: 14px;
		font-family: "Arial";
		color: rgb(192, 192, 192);
		font-style: italic;
		line-height: 1.714;
		text-align: center;
		padding: 20px 30px;
	}
</style>


