import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Posts from '@/components/Posts'




Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/pricing/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/pricing/posts',
      name: 'Posts',
      component: Posts
    },
  ],
  mode: 'history'
})



